  async getAllSold() {
    try {
      const query = new Parse.Query('Sold');
      const results: any[] = await query.equalTo('user', Parse.User.current()).include('brand').include('model').include('category').descending('createdAt').find();
      const sold: ISold[] = ParseHelper.arrayToJSON(results);
      console.log('Got request sold: ', sold);
      for (const el of sold.slice().reverse()) {
        if(!await PaginationHelper.checkExisting(el,this.soldStore.sold)){
          this.soldStore.addSoldRequest(el);
        }
      }
    } catch (e) {
      console.log(e);
      return 'Cant get sold';
    }
  }



